import React, { useState } from 'react'
import { CSSTransition } from 'react-transition-group'
import './main.css'

function Main(props) {
  return (
    <div className='main'>
      {
        props.value.map(item => <Item key={item.name} value={item}/>)
      }
      <CSSTransition in={props.hint} timeout={500} classNames='main-scroll-hint'>
        <div className='main-scroll-hint'><span>滑動看更多資料</span></div>
      </CSSTransition>
    </div>
  );
}

function Item(props) {
  const [src, setSrc] = useState('/images/' + props.value.image + '_01.png')
  const [mouseOver, setMouseOver] = useState(false)

  const type = props.value.type
  var classType = 'main-item'
  switch(type) {
    case 1: 
      classType += ' main-item-limit'
      break
    case 2: 
      classType += ' main-item-event'
      break
    case 3: 
      classType += ' main-item-consume'
      break
    default:
      break
  }

  const hint = [
    { tag: '限定', color: 'red' },
    { tag: '活動', color: 'blue' },
    { tag: '商品', color: 'green' }
  ]

  return (
      <a className={classType} href={props.value.url} target='_blank' rel='noopener noreferrer'
        onMouseEnter={() => {
          setSrc('/images/' + props.value.image + '_02.png')
          setMouseOver(true)
        }}
        onMouseLeave={() => {
          setSrc('/images/' + props.value.image + '_01.png')
          setMouseOver(false)
        }}
      >
        <div className='main-image-div'>
          <img className='main-image' src={src} alt={props.value.name}></img>
        </div>
        <div className='main-content'>
          <span className='main-name'>{props.value.name}</span>
          { (mouseOver && type > 0) &&
            <span style={{fontSize: '16px', color: hint[type-1].color}}>{' <' + hint[type-1].tag + '>'}</span>
          }
          <div>
            <div className='main-text'>{props.value.effects[0]}</div>
            <div className='main-text'>{props.value.effects[1]}</div>
            <div className='main-text'>{props.value.effects[2]}</div>
          </div>
        </div>
      </a>
  );
}

export default Main